@import url("https://fonts.googleapis.com/css2?family=Rubik+Gemstones&display=swap");

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #141414;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: "Rubik Gemstones", cursive;
}

.App-link {
  color: #61dafb;
}

.header-items {
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 50px;
  width: 100%;
  justify-content: space-between;
}

.search-container {
  display: flex;
  column-gap: 15px;
}

.search-box {
  height: 30px;
  font-size: 20px;
}

.search-btn {
  height: 40px;
  font-size: 20px;
  background-color: #78e08f;
  font-weight: bolder;
  border-radius: 10px;
  border-style: hidden;
  color: #223132;
}

.login-btn {
  height: 40px;
  font-size: 20px;
  background-color: #d980fa;
  font-weight: bolder;
  border-radius: 10px;
  border-style: hidden;
  color: #f3f3f3;
}

.genre_row {
  display: flex;
  column-gap: 10px;
  margin: 10px;
  justify-content: center;
  padding: 10px;
}

.genre_card {
  border-radius: 10px;
  padding: 15px;
  border-style: hidden;
  background-color: #181818;
  color: white;
  font-size: 14px;
  font-weight: 800;
}

.genre_card:hover {
  transform: scale(1.2);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
